import Image from "../../Elements/Media/Image";
const Media = ({ itemData, wallID, themeID, ownerId }) => {
  const mediaType = (itemData.type === 3 || itemData.type === 5) ? true : false
  const isYoutubePost = (itemData.network.id === 7) ? true : false
  const multiImages = (itemData.imageList && itemData.imageList.length > 0) ? true : false
  const multiImagesClass = ((itemData.imageList && itemData.imageList.length) > 0 && (itemData.type === 3 || itemData.type === 5)) ? 'tb_ht_multi_images_ico_bottom' : 'tb_ht_multi_images_ico'
  const isAudio = itemData.isAudio === 1 ? true : false
  return <div className="tb_ht_media_wrap">
    {multiImages ? <div className={`${multiImagesClass} tb__icon tb-multiple`}> </div> : ''}
    {mediaType ? isYoutubePost ? <div className="tb_ht_youtube_ico tb__icon tb-youtube-default"><div className="tb_color_icon__ tb_d_youtube_path_1"><div></div></div><div className="tb_color_icon__ tb_d_youtube_path_2"><div></div></div></div> :
      isAudio ? <div className="tb_ht_audio_ico tb__icon tb-audio"><div></div></div> :
        <div className="tb_ht_video_ico tb__icon tb-play-circle"><div></div></div> : ''}
    <Image ImageClass="tb_ht_image" data={itemData} wallID={wallID} themeID={themeID} ownerId={ownerId} size={100} />
  </div>
}
export default Media