import React, { PureComponent, Suspense } from "react";
const DefaultNetwork = React.lazy(() => import("./DefaultNetwork"));

export default class Network extends PureComponent {
  render() {
    
    const { network, networkClass, ThemeRule } = this.props;
    const isDefaultIcon = ThemeRule.iconType === 1 ? (network.id === 7 || network.id === 4) ? false : true : false

    return (isDefaultIcon ? <div className={`${networkClass} tb__icon tb-${network.icon}`} style={{ color: [69,65].includes(ThemeRule.themeId) && ThemeRule.iconColor=='#ffffff ' ? '#000' : ThemeRule.iconColor  }}><div></div></div> : <Suspense fallback={<></>}> <DefaultNetwork network={network} ThemeRule={ThemeRule} networkClass={networkClass}  /></Suspense>);
  }
}

