import React, { PureComponent } from "react";
import { Splide, SplideSlide, SplideTrack } from '@splidejs/react-splide';
import Card from "./Elements/Card";
import "./scss/HighlightTheme.scss";
import '../scss/ThemesSplide.scss'

export default class HighlightTheme extends PureComponent {
  constructor(props) {
    super(props)
    // this.secondaryRef = React.createRef();
    this.inputRefs = {};
  }
  componentDidMount() {
    setTimeout(() => {
      const classNameRole = document.querySelectorAll('.splide__slide');
      classNameRole.forEach((element, index) => {
        element.removeAttribute("role");
      });
    }, 100);
  }
  componentDidUpdate() {
    const value = Object.values(this.inputRefs)[0];
    const { postData, completeDataObject, onClickPopUpSlider, parentID } = this.props;
    if (value && Object.values(value)[5].length > 0) {
      let data = Object.values(value)[5]
      data.map((item, itemIndex) => {
        if (item.classList) {
          const filterID = String(item.classList.value).split(" ").filter((fCheck) => String(fCheck).includes("tb_"))
          var getID = filterID[0]
          if (String(filterID[0]).includes("tb_")) {
            getID = String(filterID[0]).replace("tb_", "")
          }
          const findIndex = postData.findIndex((find) => find == getID)
          let clickD = document.querySelectorAll(`#${item.id}`)
          for (let index = 0; index < clickD.length; index++) {
            let clickDD = clickD[index];
            let p_id = clickDD.getAttribute('p_id')
            if (String(parentID) === String(p_id)) {
              clickDD.addEventListener('click', event => {
                event.preventDefault();
                const itemData = completeDataObject[getID]
                onClickPopUpSlider(findIndex, itemData)
              });
            }
          }
        }
      })
    }
  }
  onUpdateData = event => {

  }
  render() {
    const { postData, completeDataObject, wall, clickToShowPopUp, onClickToCTA, parentID } = this.props;
    const inputName = parentID;
    const numberOfCoumn = wall.ThemeRule.numberOfCoumn;
    const mobileColumn = wall.ThemeRule.mobileColumn;
    const slidePost = wall.ThemeRule.slidePost;
    const slideDuration = wall.ThemeRule.slideDuration;
    const autoScrollStatus = wall.Personalization.autoScrollStatus;
    //const rowCount = wall.ThemeRule.multiRow;

    const settings = {
      type: autoScrollStatus === 1 ? false : 'loop',
      rewind: false,
      speed: 1000,
      interval: slidePost === 1 ? (parseInt(slideDuration) * 1000) : 5000,
      perPage: numberOfCoumn > 0 ? numberOfCoumn : 6,
      perMove: 1,
      gap: 0,
      autoplay: slidePost === 1 ? true : false,
      padding: 0,
      pagination: false,
      arrows: true,
      breakpoints: {
        560: {
          perPage: mobileColumn > 0 ? mobileColumn : 1,
        },
        767: {
          perPage: mobileColumn > 0 ? mobileColumn : 2,
        },
        991: {
          perPage: mobileColumn > 0 ? mobileColumn : 3,
        },
        1200: {
          perPage: numberOfCoumn > 0 ? numberOfCoumn : 4,
        },
        1400: {
          perPage: numberOfCoumn > 0 ? numberOfCoumn : 5,
        },
        1600: {
          perPage: numberOfCoumn > 0 ? numberOfCoumn : 6,
        }
      }
    };
    return (parentID != "" ?
      <div className="tb_ht_post_container">
        <Splide hasTrack={false} className="tb_ht_post_slider" options={settings} ref={(parentID) => this.inputRefs[inputName] = parentID} >
          <SplideTrack>
            {
              ((postData && postData.length > 0)) && postData.map((item, index) => {
                const cardData = completeDataObject[item];
                return <SplideSlide key={index} style={{ margin: 0, padding: 0 }} className={`tb_${cardData.id}`} p_id={parentID} ><Card itemData={cardData} itemIndex={index} personalization={wall.Personalization} ThemeRule={wall.ThemeRule} clickToShowPopUp={clickToShowPopUp} wallID={wall.Wall.id} onClickToCTA={onClickToCTA} /></SplideSlide>
              })}
          </SplideTrack>

          <div className="splide__arrows splide__arrows--ltr tb_ht_arrow_wrapper_">
            <div className="tb_ht_arrow splide__arrow splide__arrow--prev tb_ht_arrow tb_ht_arrow_left__ tb__icon tb-arrow-left-alt" onClick={this.onUpdateData} > </div>
            <div className="tb_ht_arrow splide__arrow splide__arrow--next  tb_ht_arrow_right__ tb__icon tb-arrow-right-alt"></div>

          </div>
        </Splide>
      </div> : null
    );
  }
}
