import React, { PureComponent } from "react";
import { POPUP_IMAGE_RENEW_REQUEST } from '../../../../actions/themeActions'
import { getImageSize } from 'react-image-size';
import { GET_MEDIA_NAME } from '../../../../utils'
import { CloudUrl } from "../../../../constants";


export default class Image extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      loadError: false,
      imgUrl: this.props.data.postFileNew,
      percentHeight: '100',
      newSize: {
        height: 100,
        width: 100
      },
      mediaSize: {
        height: 100,
        width: 100
      }
    }
    this.ImgWrap = React.createRef();
  }

  componentDidMount() {
    this.updateDimensions();
    if (!this.props.size) {
      this.onMediaLoad();
    }
  }
  updateDimensions = () => {
    let newSize = this.ImgWrap.current.getBoundingClientRect();
    if (newSize) {
      this.setState({
        newSize: {
          height: newSize.height.toFixed(),
          width: newSize.width.toFixed()
        }
      });
    }
  };

  onMediaLoad = async (props) => {
    const { data } = this.props;
    const { imgUrl } = this.state;
    if (data && Object.keys(data).length > 0 && [4, 5, 3].includes(data.type)) {
      if (data.mediaHeight && data.mediaHeight != "" && data.mediaWidth && data.mediaWidth != "") {
        this.setState({
          mediaSize: {
            height: data.mediaHeight,
            width: data.mediaWidth,
          }
        })
      } else {
        try {
          const { width, height } = await getImageSize(imgUrl);
          this.setState({
            mediaSize: {
              height: height,
              width: width,
            }
          })
        } catch (error) {
          this.setState({
            loadError: true,
            mediaSize: {
              height: 100,
              width: 100
            }
          })
        }
      }
    }
  }

  onLoad = event => {
    if (event.target.getAttribute("data-load") == 1) this.setState({ imgUrl: event.target.src })
  }


  render() {
    const { ImageClass, data, wallID, themeID, ownerId, size } = this.props
    const { imgUrl, newSize, mediaSize } = this.state

    const imageStyle = {
      paddingBottom: size ? `${size}%` : (mediaSize.height * 100) / mediaSize.width + '%'
    };

    return <div className={`${ImageClass}_wrap_ mediaHolder${data.id}`} style={imageStyle} ref={this.ImgWrap}>
      <img
        className={ImageClass}
        role="img"
        src={imgUrl}
        //data-src={imgUrl}
        height={300}
        width={300}
        data-link={data.link}
        data-load="0"
        data-network={data.network.id}
        data-wall-id={wallID}
        data-owner-id={ownerId}
        data-item-id={data.id}
        data-filter-id={data.filterId}
        theme-id={themeID}
        onLoad={this.onLoad}
        onError={!data.stories ? (e) => {
          e.target.src = `${CloudUrl}/media/images/no-image.svg`
          POPUP_IMAGE_RENEW_REQUEST(e)
        } : null}
        alt={GET_MEDIA_NAME(imgUrl)}
      />
    </div>
  }
}
